<template>
  <ContentCard class="content">
    <ContentWrapper :pagination="pagination" @filter-change="handleFilterChange">
      <!--  -->
      <el-form inline ref="form" v-model="filter" label-width="90px" class="form">
        <el-form-item class="el-col-6">
          <el-input v-model="filter.tabId" placeholder="ID"/>
        </el-form-item>
        <el-form-item class="el-col-6">
          <el-input v-model="filter.tabName" placeholder=""/>
        </el-form-item>
        <el-form-item class="el-col-6">
          <el-select v-model="filter.tabType" placeholder="">
            <el-option
              v-for="(item, index) in tabTypeOption"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item class="el-col-6">
          <el-select v-model="filter.hasSubTab" placeholder="tab">
            <el-option value></el-option>
            <el-option value="1"></el-option>
            <el-option value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="el-col-6">
          <el-select v-model="filter.tabCategory" placeholder="">
            <el-option value></el-option>
            <el-option
              v-for="(item, index) in businessType"
              :key="index"
              :label="item.dictCnName"
              :value="item.dictId"
            />
          </el-select>
        </el-form-item>
        <el-form-item class="el-col-6">
          <el-select v-model="filter.tabAppid" placeholder="AppId">
            <el-option
              v-for="item in appIdType"
              :key="item.dictEnName"
              :label="item.dictCnName"
              :value="item.dictEnName"
            />
          </el-select>
        </el-form-item>
        <el-form-item class="el-col-6">
          <el-select v-model="filter.tabStatus" placeholder="">
            <el-option value></el-option>
            <el-option
              v-for="item in tabStatusOption"
              :key="item.label"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item class="el-form-item-submit">
          <el-button type="primary"  @click="handleFilterChange"></el-button>
          <el-button  @click="handleFilterReset"></el-button>
        </el-form-item>
      </el-form>
      <!-- end -->
      <!-- <div class="btns">
        <el-button type="primary" icon="el-icon-plus" @click="addTabInfo"></el-button>
        <el-button type="primary" icon="el-icon-edit" @click="editData"></el-button>
        <el-button type="primary" icon="el-icon-delete" @click="batchDel"></el-button>
      </div> -->
         <ButtonGroupForListPage 
        pageName='tab' 
        @add="addTabInfo"
        @edit="editData"
        @delete="batchDel"
        >
        </ButtonGroupForListPage>
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"
      />
    </ContentWrapper>
  </ContentCard>
</template>

<script>
import { ContentWrapper, Table } from 'admin-toolkit'
import ButtonGroupForListPage from '@/components/ButtonGroupForListPage'
export default {
  components: {
    ContentWrapper,
    Table,
    ButtonGroupForListPage
  },

  data() {
    return {
      selected: [],
      tabTypeOption: [
        { label: '', value: '5' },
        { label: '', value: '4' },
        { label: '', value: '2' },
        { label: '', value: '1' }
      ],
      tabStatusOption: 
      [
        { label: '', value: '0' },
        { label: '', value: '1' },
        { label: '', value: '2' },
        { label: '', value: '3' },
        { label: '', value: '4' },
        { label: '', value: '5' }
      ],
      businessType: [],
      filter: {
        idPrefix: 10
      },
      appIdType: [],
      filterSchema: null,
      pagination: {
        currentPage: 1
      },
      table: {
        props: {},
        data: [],
        header: [
          {
            label: 'ID',
            prop: 'tabId'
          },
          {
            label: '',
            prop: 'tabName',
            render: (createElement, { row }) => {
              return createElement(
                'el-button',
                {
                  attrs: {
                    type: 'text'
                  },
                  on: {
                    click: () => {
                      this.handleRead(row)
                    }
                  }
                },
                row.tabName
              )
            }
          },
          {
            label: '',
            prop: 'tabType',
            formatter: function(row) {
              const v = row.tabType
              return (
                {
                  '1': '',
                  '2': '',
                  '4': '',
                  '5': ''
                }[v + ''] || ''
              )
            }
          },
          {
            label: '',
            prop: 'businessCategoryName'
          },
          {
            label: '',
            prop: 'tabResource',
            formatter: function(row) {
              const v = row.tabResource
              return (
                { o_tencent: '', o_iqiyi: '', o_voole: '' }[
                  v + ''
                ] || ''
              )
            }
          },
          {
            label: 'AppId',
            prop: 'tabAppid',
            formatter: row => {
              return this.appIdType.map(item => {
                if (row.tabAppid.toString() === item.dictEnName) {
                  return item.dictCnName
                }
              })
            }
          },
          {
            label: '/',
            prop: 'tabStatus',
            formatter: row => {
              const status = row.tabStatus
              const currentVersion = row.currentVersion
              return (
                currentVersion +
                '/' +
                this.tabStatusOption
                  .map(function(item) {
                    if (status.toString() === item.value) {
                      return item.label
                    }
                  })
                  .join('')
              )
            }
          },
          {
            label: '',
            prop: 'duplicateVersion',
             render: (createElement, { row }) => {
              return createElement(
                'el-button',
                {
                  attrs: {
                    type: 'text'
                  },
                  on: {
                    click: () => {
                      this.handleRead(row, row.duplicateVersion)
                    }
                  }
                },
                row.duplicateVersion
              )
            }
          },
          {
            label: '',
            prop: 'lastUpdateDate'
          },
          {
            label: '',
            prop: 'auditor'
          },
          {
            label: '',
            prop: 'modifierName'
          },
          {
            label: '',
            prop: 'oper'
          }
        ],
        selected: [],
        selectionType: 'multiple'
      }
    }
  },

  methods: {
    handleRead(row, version) {
      this.$emit('read', row, version)
    },
    /**
     * 
     */
    fetchData() {
      this.handleAllRowSelectionRemove()
      const filter = this.parseFilter()
      if (filter.tabId && !/^\d+$/.test(filter.tabId)) {
        return this.$message({
          type: 'error',
          message: 'ID '
        })
      }
      this.$service.tabInfoList(filter).then(data => {
        this.pagination.total = data.total
        this.table.data = data.rows
      })
    },
    parseFilter() {
      const { filter, pagination } = this
      if (pagination) {
        filter.page = pagination.currentPage
        filter.rows = pagination.pageSize
      }
      return filter
    },
   handleFilterChange(type) {
      if(this.$validateId(this.filter.tabId)) {
        if (type === 'query') {
          if (this.pagination) {
            this.pagination.currentPage = 1
          }
        }
      }
      this.fetchData() 
    },
    handleFilterReset() {
      this.filter = {
        idPrefix: this.$consts.idPrefix
      }
      this.pagination.currentPage = 1
      this.fetchData()
    },
    addTabInfo() {
      this.$emit('create')
    },
    editData() {
      const table = this.table
      const selected = this.table.selected
      const target = table.data[selected[0]]
      this.$emit('edit', target)
    },
    batchDel() {},
    /**
     * 
     */
    handleRowSelectionAdd(targetItem) {
      this.selected.push(targetItem.tabId)
      this.updateTableSelected()
    },
    handleRowSelectionRemove(targetItem) {
      this.selected = this.selected.filter(item => {
        return item !== targetItem.tabId
      })
      this.updateTableSelected()
    },
    handleAllRowSelectionChange(value) {
      if (value) {
        this.table.data.forEach(this.handleRowSelectionAdd)
      } else {
        this.selected = []
        this.table.selected = []
      }
    },
      handleAllRowSelectionRemove () {
      this.selected = []
      this.table.selected = []
    },
    updateTableSelected() {
      const table = this.table
      const newSelectedIndex = this.selected
      table.selected = table.data.reduce((result, item, index) => {
        if (newSelectedIndex.indexOf(item.tabId) > -1) {
          result.push(index)
        }
        return result
      }, [])
    },
    getBusinessType() {
      this.$service.getDictType({type: 'businessType'}).then(data => {
        this.businessType = data
      })
    }
  },
  created() {
    this.$service.getDictType({type: 'appIdType'}).then(data => {
      this.appIdType = data
      this.fetchData()
    })
    this.getBusinessType()
  }
}
</script>

<style lang = 'stylus' scoped>
.form >>>.el-form-item__content
  width: 83.33333%
  .el-select
    width: 100%
.form >>>.el-form-item
  margin-right: 0px
.el-form-item-submit
  width: 200px
</style>